<template>
<!-- "buefy": "^0.9.2", -->
    <section v-if="ready">
      <div v-if="selected">
        <!-- FORM -->
      </div>
      <div v-if="modal">
        <div class="row justify-content-center">
          <div class="col-md-6">
           
          </div>
        </div>
      </div>
      <div style="overflow:scroll;padding:10px;" class="scrollbarnya">
            <div class="sm-form float-right">
                <input type="text" id="search" name="search" class="form-control form-control-sm" placeholder="search" v-model="search" >
           </div>
              <!-- <button type="button" class="btn btn-sm btn-dark  mr-1" @click="pdf">PDF</button> -->
              <button type="button" class="btn btn-sm btn-dark  mr-1" @click="excel">Excel</button>
              <button type="button" class="btn btn-sm btn-danger  mr-1" @click="hapusSemuaData" v-if="checkedRows.length>0">Delete Selected</button>
            <select class='form-control mt-3' style="width:20vw;" v-model="perPage">
            <option value="5">5</option>
            <option value="10">10</option>
            <option value="50">50</option>
            <option value="50">100</option>
            <option value="1000">semua</option>
            </select>
            <!-- b-table -->
        <b-table
            :data="td"
            :sticky-header="false"
            :selected.sync="selected"
            class="text-xs p-2"
            :checked-rows.sync="checkedRows"
            :is-row-checkable="(row) => row.id"
            :checkable="checked"
            :checkbox-position="'left'"
            :per-page="perPage" :bordered="true" :striped="true" :narrowed="true" :hoverable="true" :mobile-cards="false" :paginated="true" :sort-icon="'arrow-up'" :sort-icon-size="'is-small'" :pagination-simple="false" :default-sort-direction="'asc'" :pagination-rounded="true" 
            >
            <b-table-column 
            :visible="!hides.includes(item.field)"
            :searchable="['nama field buat search'].includes(item.field)"
            v-for="(item, index) in keys" :key="index+'keys'"
            :field="item.field" :label="item.label" sortable  v-slot="props" >
            <span class="text-sm">
              <span v-if="typeof props.row[item.field] != 'object' || props.row[item.field]!=''?true:false">
                {{props.row[item.field]}}
              </span>
              <span v-else>
                <button type="button" class="btn btn-sm btn-dark  " @click="ambilData(props.row[item.field],props)">
                  Show Data
                </button>
                <div v-if="modal==props.index" class="p-2">
                  <table class="table table-sm table-responsive">
                    <tr v-for="(item, index) in datamodalkey" :key="index+'datamodalkey'" v-if="item!='id'">
                      <td class="capitalize font-semibold">{{item}}</td>
                      <td class="pl-2 pr-2">:</td>
                      <td>{{datamodal[item]}}</td>
                    </tr>
                  </table>
                </div>
              </span>
              <!-- cek apakah type data yang muncul adalah object atau tidak, jika iya maka ambil value dari key ke 2, ini karena behavior dari laravel relationship -->
            </span>
            </b-table-column>
             <!-- <b-table-column 
             v-if="checked"
            >
            <span class="text-sm" >
              <button type="button" class="btn btn-sm btn-danger text-xs" @click="hapusData">Del</button>
            </span>
            </b-table-column> -->
        </b-table>
      </div>
    </section>
</template>

<script>
    export default{
      props:['tables','hide'],
        data() {
            return {
                ready:false,
                checked:true,
                datanya:[],
                keys:[],
                perPage:50,
                search:"",
                checkedRows:[],
                selected:null,
                modal:false,
                datamodal:{},
                datamodalkey:[]
            }
        },
        watch:{
          selected(){
            this.$emit('selected',JSON.parse(JSON.stringify(this.selected)))
          }
        },
        methods: {
          ambilData(data,props){
            this.modal=props.index;
            this.datamodal=data;
            this.datamodalkey=Object.keys(data);
            this.$forceUpdate();
          },
          pdf(){
            this.checked=false;
            this.ready=false;
              setTimeout(() => {
                this.ready=true;
                this.$forceUpdate();
                setTimeout(() => {
                  this.$emit('pdf',true);
                  var worker = html2pdf();
                  var element=document.querySelector('.table-wrapper');
                  console.log(element)
                  var worker = html2pdf().from(element).save();
                  this.checked=true;
                  this.ready=false;
                  setTimeout(() => {
                    this.ready=true;
                    this.$forceUpdate();
                  }, 300);
                }, 300);
              }, 300);
          },
          excel(){
              this.$emit('excel',true);
          },
          hapusData(){
              this.$emit('hapusdata',true);
          },
          hapusSemuaData(){
            if(this.checkedRows.length>0){
              this.$emit('hapussemuadata',this.checkedRows);
            }
          },
         async getData(){
            let that =this
            if(this.tables){
              let data = this.tables
              console.log(data)
              that.datanya = data
              that.keys = Object.keys(data[0])
              that.keys = that.keys.map(e=>{
                return {
                  field:e,
                  label:e.toUpperCase()
                }
              })
              this.ready = true
              this.$forceUpdate()
            }
            this.$forceUpdate()
          }
        },
       async mounted() {
         this.getData()
          setTimeout(() => {
            this.$el.querySelectorAll('th').forEach(e=>{
              e.style.background = 'black';
              e.style.color = "white";
            })
          }, 2000);
        },
         computed:{
          hides(){
             let data =this.hide?this.hide:[] 
             data.push('createdAt','updateAt')
             return data
           },
          td(){
            let data = this.datanya
            //! search fungsi
            let keys = Object.keys(data[0]);
            let that=this
            data = data.filter((v, i, a) => {
              let hasil = false;
              keys.filter(key => {
                if (typeof v[key] == "string") {
                  if (v[key].toLowerCase().indexOf(this.search.toLowerCase()) != -1) {
                    hasil = true;
                  }
                } else {
                  if (v[key] != null) {
                    if (v[key].toString().indexOf(this.search) != -1) {
                      hasil = true;
                    }
                  } else {
                  }
                }
              });
              if (hasil) return v;
            });
            return data
          }
        },  
    }
</script>
<style >
.is-sticky-column-one {
    background: #23d160 !important;
    color: white !important;
}
.is-sticky-column-two {
    background: #167df0 !important;
    color: white !important;
}
.scrollbarnya::-webkit-scrollbar {
  width: 0px;
  height:0px;
}

/* Track */
.scrollbarnya::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey; 
  border-radius: 0px;
}
 
/* Handle */
.scrollbarnya::-webkit-scrollbar-thumb {
  cursor: grab;
  background: black; 
  border-radius: 0px;
}

/* Handle on hover */
.scrollbarnya::-webkit-scrollbar-thumb:hover {
  background: #b30000; 
}
</style>